const apiList = ['layoutEditor', 'sidebar', 'events'];
export class ProtoAPI {
    constructor(_initializedCb) {
        this._initializedCb = _initializedCb;
    }
    get layoutEditor() { return this._layoutEditor; }
    set layoutEditor(api) {
        if (!this._layoutEditor) {
            this._layoutEditor = api;
            this._checkApiStatus();
        }
    }
    get sidebar() { return this._sidebar; }
    set sidebar(api) {
        if (!this._sidebar) {
            this._sidebar = api;
            this._checkApiStatus();
        }
    }
    get events() { return this._events; }
    set events(api) {
        if (!this._events) {
            this._events = api;
            this._checkApiStatus();
        }
    }
    _checkApiStatus() {
        for (const api of apiList) {
            if (!this[api]) {
                return;
            }
        }
        this._initializedCb();
    }
}
